<template>

    <tituloBusqueda titulo="Consulta de Plazas" descripcion="Realiza la busqueda de una plaza de acuerdo a los filtros."></tituloBusqueda>

    <div class="formularios">
        <div class="row">
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="plaza" @keypress="validaPlaza" @keyup="mayusc" class="form-control"
                        aria-describedby="id" placeholder="PLAZA">
                </div>
            </div>
            <div class="col" >
                <input_seccion @seccion_val="obten_seccion"></input_seccion>
            </div>
            <div class="col">
                <input_nomina></input_nomina>
            </div>
        </div>
        <div class="row">
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="paterno" @keypress="validaNombreComp" @blur="algo" @keydown="mayusc"
                        class="form-control" aria-describedby="id" placeholder="APELLIDO PATERNO">
                </div>
            </div>
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="materno" @keypress="validaNombreComp" @keyup="mayusc"
                        class="form-control" aria-describedby="curp" placeholder="APELLIDO MATERNO">
                </div>
            </div>
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="nombre" @keypress="validaNombreComp" @keyup="mayusc"
                        class="form-control" aria-describedby="rfc" placeholder="NOMBRE">
                </div>
            </div>
        </div>
        <div class="buttons ">
            <a class="btn-outline primary " @click="buscarplazas">
                Buscar
            </a>
        </div>



    </div>
    <!--    <div class="tbl-resultados">
            <div class="tbl-header row">
                <div class="tbl-header-data tbl_short-s">#</div>
                <div class="tbl-header-data tbl_long">PLAZA</div>
                <div class="tbl-header-data">SECCION</div>
                <div class="tbl-header-data tbl_long">MAESTRO</div>
                <div class="tbl-header-data">NOMINA</div>
            </div>
            <div class="tbl-maestros">
                <div class="tbl-data row">
                    <div class="tbl-dato tbl_short-s">1</div>
                    <div class="tbl-dato tbl_long"><a href="detalle.html">HEHA830928HPLRRB03</a></div>
                    <div class="tbl-dato">10 </div>
                    <div class="tbl-dato tbl_long">HERNANDEZ GONZALES ARTURO </div>
                    <div class="tbl-dato">CONTRATURNO</div>
                </div>
               
            </div>

        </div>-->


    <table class="table table-hover tbl-resultados">
        <thead>
            <tr>
                <th scope="col">#</th>
                <th scope="col">PLAZA</th>
                <th scope="col">SECCION</th>
                <th scope="col">MAESTRO</th>
                <th scope="col">NOMINA</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(plza,index) in arr_plazas" :key="{index}">
                <th scope="row">{{index+1}}</th>
                <td>{{plza.plaza.toUpperCase()}}</td>
                <td>{{plza.id_seccion}}</td>
                <td>{{(plza.ap_paterno+' '+plza.ap_materno+' '+plza.nombre).toUpperCase()}}</td>
                <td>{{plza.nomina.toUpperCase()}}</td>
            </tr>

        </tbody>
    </table>
    <miModal titulo='PLAZAS' :mensaje=mensajeModal></miModal>
    <myFooter></myFooter>
</template>

<script>

import { defineAsyncComponent } from 'vue'
import axios from 'axios'

const input_seccion = defineAsyncComponent(()=>import('@/modules/shared/inputs/seccion.vue'))
const input_nomina = defineAsyncComponent(()=>import('@/modules/shared/inputs/nomina.vue'))
const myFooter = defineAsyncComponent(()=>import('@/modules/shared/components/Footer.vue'))
const tituloBusqueda = defineAsyncComponent(()=>import('@/modules/shared/components/TituloBusqueda.vue'))
const miModal = defineAsyncComponent(()=>import('@/modules/shared/components/ModalAceptar.vue'))

//Variables de captura
let plaza=''
let paterno=''
let materno = ''
let nombre = ''
let seccion = ''
let nomina = ''

//Variables mensajeModal
let mensajeModal =''

export default ({
    components:{
        input_seccion,
        input_nomina,
        myFooter,
        tituloBusqueda,
        miModal
    },
    data() {
        return {
            plaza, nombre, paterno, materno, seccion, nomina,
            mensajeModal,
            arr_plazas: []
        }
    },
    methods: {
        async buscarplazas(){

            let params = {
                    plaza: this.plaza,
                    seccion: this.seccion,
                    ap_pat: this.paterno,
                    ap_mat: this.materno,
                    nombre: this.nombre,                    
                    nomina: this.nomina
                }
                console.log("Buscando..")
                try{
                    const response= await this.obtenerPlazas(params)
                    const {estatus,errorText,num_registros,resultado} = response

                    console.log(response)
                    
                    if (estatus ==='error'){
                        this.mensajeModal = errorText
                        $('#staticBackdrop2').modal('show')
                    }
                    
                    if (estatus ==='ok'){
                        if (num_registros<1){
                            this.mensajeModal = 'No se encontro información'
                            $('#staticBackdrop2').modal('show')
                        }
                        else{
                            this.arr_plazas = response.resultado
                            console.log("RESULTADO::",resultado)
                        }
                    }
                    
                }catch(error){
                    console.log(error)
                    this.mensajeModal = error.data.errorText 
                    $('#staticBackdrop2').modal('show')
                }
        },
        async obtenerPlazas(params){
            try{
                    var json = JSON.stringify(params);

                    console.log(json)
                    const response = await axios.get(`http://localhost:8081/api/plazas`,{
                        params:{
                            plaza: this.plaza,
                            seccion: this.seccion,
                            ap_pat: this.paterno,
                            ap_mat: this.materno,
                            nombre: this.nombre,                    
                            nomina: this.nomina
                        }
                    })
                    return response.data
                }
                catch(error){
                    console.log(error)
                    return error.response.data
                }

        },
        /*mayuscula() {
            this.plaza = this.plaza.toUpperCase()
            this.nombre = this.nombre.toUpperCase()
            this.paterno = this.paterno.toUpperCase()
            this.materno = this.materno.toUpperCase()
        },*/
        obten_seccion(secc){
                this.seccion = secc
            },
        validaPlaza(e) {
            if (this.plaza.length >= 16)
                e.preventDefault();
        },
        validaNombreComp(e) {
            if (e.srcElement.value.length >= 25)
                e.preventDefault();

        },
        mayusc(e) {
            e.srcElement.value = e.srcElement.value.toUpperCase()

        },
        algo(e){
            e.srcElement.value = e.srcElement.value.toUpperCase()
        }

    },
})
</script>
>

